#span{
    color: #ab4493; 
}
section{
    
    width: 100%;
    flex-wrap: wrap;
    min-width: 300px;
    height: 885px;
    padding: 120px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #0062be;
    transition: 0.5s;
}

.content{
    position: relative;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.content .textBox{
    position: relative;
    max-width: 600px;
    width: 100%;
    flex-wrap: wrap;
    
}
.content .textBox h2{
    color: #fff;
    font-size: 4em;
    line-height: 1.5em;
    font-weight: 900;
    text-transform: uppercase;
}
.content .textBox h2 span{
    font-size: 2em;
}
.content .textBox p{
    color: #fff;
}
.content .textBox a{
    display: inline-block;
    margin-top: 20px;
    padding: 8px 20px;
    background: #fff;
    color: #111;
    border-radius: 40px;
    font-weight: 500;
    letter-spacing: 1px;
    text-decoration: none;
    text-transform: uppercase;
}
.content .imgBox{
    width: 400px;
    display:flex;
    padding-right: 0px;
    margin-top: 0px;
    justify-content: flex-end;
}
.content .imgBox img{
    max-width: 400px;
    
    overflow: hidden;
    position: relative;
}
.thumb{
    position: absolute;
    left: 28%;
    bottom: 20px;
    transform: translateX(-50%);
    display: flex;
    flex-wrap: wrap;
    
}
.thumb li{
    list-style: none;
    display: inline-block;
    margin: 0 60px;
    cursor: pointer;
    transition: 0.5s;
    
}
.thumb li:hover{
    transform: translateY(-15px)
}
.thumb li img{
    max-width: 65px;
    min-width: 25px;
    margin-bottom: 0px;
}
.sci{
    position: absolute;
    top: 50%;
    right: 30px;
    transform: translateY(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.sci li{
    list-style: none;
}
.sci li a{
    display: inline-block;
    filter: invert(1);
    margin: 5px 0;
    transform: scale(0.6);
}
.img-1{
    flex-wrap: wrap;    
}
